function callHandler(handlerName, param) {
    console.log(JSON.stringify([handlerName,JSON.stringify(param)]));
    return new Promise((resolve, reject) => {
        try {
            initBridge(bridge=>{
                if(param.isHandler){
                    return false;
                }
                param.isHandler=true;
                bridge.callHandler(handlerName, JSON.stringify(param),
                    (res) => {
                        try {
                            resolve(res);
                        } catch (e) {
                            Toast.fail(e.message);
                            reject(e);
                        }
                    });
            })
            // WebViewJavascriptBridge.callHandler(handlerName, JSON.stringify(param),
            //         (res) => {
            //             try {
            //                 Toast(res);
            //                 resolve(res);
            //             } catch (e) {
            //                 Toast.fail(e.message);
            //                 reject(e);
            //             }
            //         });
        } catch (e) {
            Toast.fail(e.message);
            reject(e);
        }

    });
}


function registerHandler(handlerName, callback) {
    initBridge((bridge) => {
        bridge.registerHandler(handlerName, (data, resCallback) => {
            log(resCallback);
            if (typeof callback == "function") {
                callback(JSON.parse(data));
            }
        });
    })
}

function log(message){
    return callHandler('log', {
        message:message||""
    });
}

export function appUpdate(url, type, state) {
    return callHandler('update', {
        url,
        type,
        state
    });
}

export function hideHeader() {
    return callHandler('hiddenHeader', {

    });
}

export function showHeader() {
    return callHandler('showHeader', {
    });
}

export function getUserInfo(callback) {
    return new Promise((resolve, reject) => {
        try {
            registerHandler('location',(data=>{
                resolve(data);
            }));
            callHandler('getUserInfo', {
            });
        } catch (e) {
            Toast.fail(e.message);
            reject(e);
        }

    });
  
}

export function share(params) {
    return callHandler('share', params);
}

export function thirdLogin(type) {
    return new Promise((resolve, reject) => {
        try {
            registerHandler('loginResult',(data=>{
                resolve(data);
            }));
            callHandler('login', {
                type:type
            });
        } catch (e) {
            Toast.fail(e.message);
            reject(e);
        }

    });
   
}

export function isApp(){
    return new Promise((resolve, reject) => {
        // alert(Cookies.get('Terminal-Type'));
        if(window.location.hostname=='127.0.0.1'||Cookies.get('Terminal-Type')=='Android'){
            resolve();
        }else{
            reject() ;
        }

    });
}
