import { appUpdate,showHeader,hideHeader,getUserInfo,thirdLogin,share,isApp,isAndroid,isIOS } from '@/common/js/app'
import {shareBaseUrl} from '@/common/js/utils'
import Cookies from 'js-cookie'

const state = {
    appVersion: {},
   
}


const mutations = {
    setAppVersion(state, appVersion) {
        state.appVersion = appVersion;
    }
}


const actions = {
    appVersion({ commit }) {
        return new Promise((resolve, reject) => {
            appUpdate("http://static.ikedou.net/xiaoguo/dist.zip","1","0").then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    showHeader({ commit }) {
        return new Promise((resolve, reject) => {
            showHeader().then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    hideHeader({ commit }) {
        return new Promise((resolve, reject) => {
            hideHeader().then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    getUserInfo({ commit }) {
        return new Promise((resolve, reject) => {
            getUserInfo().then(res => {
            
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    thirdLogin({ commit },{type}) {
        return new Promise((resolve, reject) => {
            thirdLogin(type).then(res => {
            
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    share({ commit },params) {
        return new Promise((resolve, reject) => {
            params.url=shareBaseUrl+params.url;
            params.content="我在 发现来一个不错的商品，赶快来看看吧";
            if(!params.needPic){
                delete params.imageUrl;
            }
            share(params).then(res => {
                
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    isApp({ commit },params){
        return isApp();
    },
    isAndroid({ commit },params){
        return isAndroid();
    },
    isIOS({ commit },params){
        return isIOS();
    },
    closeApp({ commit },params){
         Cookies.set('isShowGetApp','1');
    },
    isShowApp({ commit },params){
      return new Promise((resolve,reject)=>{
          if(Cookies.get('isShowGetApp')){
                resolve();
          }else{
            reject();
          }
      }) ;
   },
    location({commit},params){
        return new Promise((resolve, reject) => {
            if (navigator.geolocation){
                navigator.geolocation.getCurrentPosition((position)=>{
                    console.log(position);
                    resolve(position)
            }
            ,(error)=>{
                switch(error.code) {
                  case error.PERMISSION_DENIED:
                    reject(new Error("定位失败,用户拒绝请求地理定位"));
                    break;
                  case error.POSITION_UNAVAILABLE:
                    reject(new Error("定位失败,位置信息是不可用"));
                    break;
                  case error.TIMEOUT:
                    reject(new Error("定位失败,请求获取用户位置超时"));
                    break;
                  case error.UNKNOWN_ERROR:
                    reject(new Error("定位失败,定位系统失效"));
                    break;
                }
              });
            }else{
                reject(new Error("浏览器不支持地理定位"));
              }
        })
    }
}

export default {
    state,
    mutations,
    actions
}



